.feedback-form {
  max-width: 480px;
  background-color: rgba(4, 89, 83, .655);
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 20px;
  flex-direction: column;
  margin: auto;
  padding: 20px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 30px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  width: 100%;
  border-radius: 5px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  background-color: #c4c4c7;
  border-radius: 5px;
  padding: 10px;
}

textarea {
  resize: none;
}

